<i18n>
{
  "en": {
    "TotalOrder": "Total order",
    "Yuan": "Yuan",
    "payment": "Payment"
  },
  "zh": {
    "TotalOrder": "订单总额",
    "Yuan": "元",
    "payment": "去支付"
  }
}
</i18n>

<template>
  <div class="moneyTip">
    <div class="mask" @click="moneyHandle(false)" v-show="moneyTip"></div>
    <transition name="fade">
      <div class="container" v-show="moneyTip">
        <div class="title">{{ $t('TotalOrder') }}</div>
        <div class="money">
          <span>{{totalAmount}}</span>
          <strong>{{ $t('Yuan') }}</strong>
        </div>
        <button @click="moneyHandle(true)">{{ $t('payment') }}</button>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'moneyTip',
  props: {
    moneyTip: {
      type: Boolean,
      default: false
    },
    totalAmount: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {}
  },

  mounted () {},

  methods: {
    moneyHandle (confirm) {
      this.$emit('moneyHandle', confirm)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.moneyTip{
  .mask{
    .fs;
    background: rgba(0, 0, 0, .5);
  }
  .container{
    .wh(690px, 420px);
    border-radius: 20px;
    position: absolute;
    left: 50%;
    bottom: 90px;
    margin-left: -345px;
    background: #FFF;
    .title{
      font-size: 30px;
      margin-top: 30px;
    }
    .money{
      .wh-l(200px, 110px);
      margin: 80px auto 0 auto;
      position: relative;
      padding-left: 25px;
      span{
        .wh-f(60%, 100%);
        display: block;
        position: relative;
        font-size: 92px;
        &:after{
          content: '';
          .wh(100%, 2px);
          display: block;
          background: #F4DA21;
          position: absolute;
          left: 0;
          bottom: 0;
        }
      }
      strong{
        font-size: 30px;
        position: absolute;
        right: 15px;
        bottom: -20px;
      }
    }
    button{
      .wh-l(480px, 80px);
      position: absolute;
      left: 50%;
      bottom: -40px;
      margin-left: -240px;
      background: @themeColor;
      border-radius: 40px;
      color: #282926;
    }
  }
}

.delay-leave-active{
  transition-delay: .2s;
}
.fade-enter-active{
  transition: all .15s ease-out .2s;
}
.fade-leave-active{
  transition: all .15s ease-out;
}
.fade-enter, .fade-leave-to{
  transform: scale3d(.6,.6,.1);
  opacity: 0;
}
</style>



