<i18n>
{
  "en": {
    "RemainingTime": "Remaining time"
  },
  "zh": {
    "RemainingTime": "剩余充电时长"
  }
}
</i18n>

<template>
  <div class="chargingTime" v-if="chargingTime">
    <strong>{{ this.$t('RemainingTime') }}</strong>
    <span>{{chargingTimeValue}}</span>
  </div>
</template>

<script>
export default {
  name: 'chargingTime',
  props: {
    chargingTime: {
      type: Boolean,
      default: false
    },

    chargingTimeValue: {
      type: String,
      default: ''
    }
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.chargingTime{
  .wh(100%, 330px);
  position: absolute;
  left: 0;
  top: 0;
  background: url('../../../assets/scanning_images/spring-time.png') 0 -14px no-repeat;
  background-size: cover;
  strong{
    .wh-l(300px, 50px);
    position: absolute;
    left: 50%;
    top: 22px;
    margin-left: -150px;
    font-size: 32px;
    color: #FFF;
  }
  span{
    .wh(100%, 100%);
    display: block;
    font-size: 118px;
    line-height: 340px;
  }
}
</style>



