<i18n>
{
  "en": {
    "storeName": "Wal-Mart",
    "address": "Zhongshan avenue tangxia",
    "Charger": "Charger",
    "Borrow": "Borrow {count}",
    "Repay": "Repay {count}",
    "NAV": "NAV"
  },
  "zh": {
    "storeName": "沃尔玛",
    "address": "中山大道棠下",
    "Charger": "提供座充",
    "Borrow": "可借{count}台",
    "Repay": "可还{count}台",
    "NAV": "导航"
  }
}
</i18n>

<template>
  <div class="stroe-column">
    <div class="top bomPixel">
      <div class="store-left">
        <span></span>
      </div>
      <div class="store-right">
        <strong>{{ $t('storeName') }}</strong>
        <span>
          <img src="../../../assets/store_images/time.png" alt="">
          <b>10:00-23:00</b>
        </span>
        <span>
          <img src="../../../assets/store_images/add.png" alt="">
          <b>{{ $t('address') }}</b>
        </span>
      </div>
    </div>
    <div class="bottom">
      <div class="charger">
        <img src="../../../assets/store_images/charger-default.png" alt="">
        <strong>{{ $t('Charger') }}</strong>
      </div>
      <div class="power">
        <img src="../../../assets/store_images/baby.png" alt="">
        <strong>{{ $t('Borrow',{count: 3}) }} {{ $t('Repay',{count: 3}) }}</strong>
      </div>
    </div>
    <div class="navigate">
      <img src="../../../assets/store_images/nav.png" alt="">
      <span>{{ $t('NAV') }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'store-column',
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.stroe-column{
  .wh(720px, 340px);
  background: url('../../../assets/store_images/bg.png') -9px 0 no-repeat;
  background-size: cover;
  position: absolute;
  left: 50%;
  margin-left: -360px;
  bottom: 25px;
  padding-top: 12px;
  .navigate{
    .wh(120px, 120px);
    position: absolute;
    top: -60px;
    right: 30px;
    img{
      height: 100%;
    }
    span{
      .wh(100px, 30px);
      display: block;
      .zoom-font(0.8);
      font-size: 28px;
      color: #585757;
      position: absolute;
      left: 50%;
      bottom: 5px;
      margin-left: -38px;
    }
  }
  .top{
    .wh(98%, 200px);
    margin: 0 auto;
    position: relative;
    .store-left{
      .wh-f(30%, 100%);
      position: relative;
      span{
        .wh(150px, 150px);
        display: block;
        border-radius: 20px;
        .f-center;
        background: #CCCCCC;
      }
    }
    .store-right{
      .wh-f(70%, 100%);
      text-align: left;
      strong{
        .wh-l(100%, 86px);
        display: block;
        font-size: 30px;
        font-weight: bold !important;
      }
      span{
        .wh-l(100%, 52px);
        display: block;
        color: #A9A9A9;
        font-size: 30px;
        img{
          height: 30px;
          margin-right: 12px;
          position: relative;
          top: 5px;
        }
      }
      span:nth-child(3){
        font-size: 26px;
        img{
          left: 4px;
          margin-right: 24px;
        }
      }
    }
  }
  .bottom{
    .wh-l(98%, 100px);
    margin: 0 auto;
    color: #626262;
    font-size: 26px;
    text-align: left;
    .charger{
      .wh-f(34%, 100%);
      padding-left: 28px;
      img{
        height: 38px;
        margin-right: 12px;
        position: relative;
        top: 10px;
        left: 4px;
      }
    }
    .power{
      .wh-f(66%, 100%);
      padding-left: 28px;
      position: relative;
      img{
        height: 38px;
        margin-right: 12px;
        position: relative;
        top: 10px;
        left: 4px;
      }
    }
  }
}
</style>



