<template>
  <div class="amap">
    <div id="container"></div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { MP } from '../../../../utils/amap.js'
export default {
  name: 'amap',
  data () {
    return {
      map: null, // 地图
      geolocation: null, // 地理定位
      marksIcon1: require('../../../../assets/home_images/landmark.png'),
      marksIcon2: require('../../../../assets/home_images/baby.png'),
      marksIcon3: require('../../../../assets/home_images/charger.png')
    }
  },

  computed: {
    ...mapGetters([
      'position'
    ])
  },

  mounted () {
    // 地图初始化
    MP('dd0ab4dec22fcb03c2e6a193fd42780d').then(AMap => {
      let timer = setTimeout(() => {
        this.initMap(AMap)
        clearTimeout(timer)
      }, 260)
    }).catch(err => {
      console.log(err)
    })
  },

  methods: {
    ...mapActions([
      'setPosition'
    ]),

    // 初始化地图
    initMap (AMap) {
      let layer = new AMap.TileLayer({
        opacity: 0.4 // 透明度
      })
      this.map = new AMap.Map('container', {
        layers: [layer],
        resizeEnable: true, // 自适应大小
        center: [116.397428, 39.90923]
        // mapStyle: 'amap://styles/b981c7f090e14ad2340853d00242b4fa'
        // zoom: 13 // 初始视窗
      })
      this.map.plugin('AMap.Geolocation', () => {
        this.geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, // 是否使用高精度
          timeout: 10000,
          GeoLocationFirst: false,
          maximumAge: 0, // 定位结果缓存0毫秒，默认：0
          showButton: false,
          showMarker: false, // 定位成功后在定位到的位置显示点标记
          showCircle: false // 定位成功后用圆圈表示定位精度范围
          // zoomToAccuracy: true // 定位成功后调整地图视野范围使定位位置及精度范围视野内可见 (showCircle为true有效)
        })
        this.map.addControl(this.geolocation)
        this.geolocation.getCurrentPosition()
        AMap.event.addListener(this.geolocation, 'complete', (position) => {
          console.log((position))
          if (this.position === '') {
            // 设置地图中心点和缩放级别
            this.map.setZoomAndCenter(13, [position.position.lng, position.position.lat])
          }
          this.setMarker('marksIcon1', position.position.lng, position.position.lat)
          // 记录位置
          this.setPosition(position.position)
          console.log(this.position)
          // 显示全部点标志
          this.deviceGeoLocation()
        }) // 返回定位信息
        AMap.event.addListener(this.geolocation, 'error', (message) => {
          let pos = '{"type":"complete","position":{"P":23.127799479167,"O":113.373349066841,"lng":113.373349,"lat":23.127799},"location_type":"html5","message":"Get geolocation success.Convert Success.Get address success.","accuracy":148,"isConverted":true,"status":1,"addressComponent":{"citycode":"020","adcode":"440106","businessAreas":[{"name":"天河公园","id":"440106","location":{"P":23.129927179487183,"O":113.36488121794878,"lng":113.364881,"lat":23.129927}},{"name":"棠下","id":"440106","location":{"P":23.12904394281045,"O":113.37899883986933,"lng":113.378999,"lat":23.129044}},{"name":"东圃","id":"440106","location":{"P":23.121330761883733,"O":113.40506492121466,"lng":113.405065,"lat":23.121331}}],"neighborhoodType":"","neighborhood":"","building":"海天楼","buildingType":"商务住宅;楼宇;商务写字楼","street":"建中路","streetNumber":"3号","province":"广东省","city":"广州市","district":"天河区","township":"天园街道"},"formattedAddress":"广东省广州市天河区天园街道海天楼广海大厦","roads":[],"crosses":[],"pois":[],"info":"SUCCESS"}'

          let position
          position = JSON.parse(pos)
          // 设置地图中心点和缩放级别
          this.map.setZoomAndCenter(13, [position.position.lng, position.position.lat])
          this.setMarker('marksIcon1', position.position.lng, position.position.lat)
          // 记录位置
          this.setPosition(position.position)
          // 显示全部点标志
          this.deviceGeoLocation()

          console.log(message)
        }) // 返回定位出错信息
      })
    },

    // 定位控件
    setCenter () {
      if (this.position === '') {
        this.geolocation.getCurrentPosition()
      } else {
        this.map.panTo([this.position.lng, this.position.lat])
      }
    },

    // 设置点标记 (marksIcon1 定位图标、marksIcon2 充电宝图标、marksIcon3 座充图标)
    setMarker (type, lng, lat) {
      // console.log(type, lng, lat)
      let w
      let h
      let size = {
        'marksIcon1': {w: 15, h: 35},
        'marksIcon2': {w: 39, h: 42.5},
        'marksIcon3': {w: 39, h: 42.5}
      }
      w = size[type].w
      h = size[type].h

      var marker = new AMap.Marker({
        position: new AMap.LngLat(lng, lat),
        offset: new AMap.Pixel(-w / 2, -h),
        icon: new AMap.Icon({
          size: new AMap.Size(w, h), // 图标大小
          image: this[type],
          imageSize: new AMap.Size(w, h)
        }),
        map: this.map,
        zIndex: type === 'marksIcon1' ? 999 : 100
      })
      marker.setMap(this.map)
    },

    // 显示所有设备位置
    deviceGeoLocation () {
      console.log(this.position.lng, this.position.lat)
      // 点标记数组
      let data = []
      let imageArray = ['marksIcon2', 'marksIcon3']
      for (let i = 0; i < 20; i++) {
        data.push({
          lnglat: [this.position.lng + (0.02 - Math.random() * 0.04), this.position.lat + (0.02 - Math.random() * 0.02)],
          image: imageArray[Math.floor(Math.random() * 2)],
          id: i
        })
      }
      console.log(data)

      for (let i = 0; i < data.length; i++) {
        this.setMarker(data[i].image, data[i].lnglat[0], data[i].lnglat[1])
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.amap{
  .fs;
  #container{
    .fs;
    .amap-logo {
      display: none !important;
    }
    .amap-copyright {
      bottom:-100px;
      display: none;
    }
  }
}
</style>



