<i18n>
{
  "en": {
    "NotLoggedIn": "Not logged in",
    "MyWallet": "My wallet",
    "TheOrder": "The order",
    "AboutUs": "About us",
    "Opinion": "Opinion",
    "CheckForUpdates": "Check for updates",
    "Language": "Language",
    "DiscoverNewVersions": "Discover new versions",
    "describe": "New version and updated content",
    "item": "Cancel | Confirm"
  },
  "zh": {
    "NotLoggedIn": "未登录",
    "MyWallet": "我的钱包",
    "TheOrder": "我的订单",
    "AboutUs": "关于我们",
    "Opinion": "意见反馈",
    "CheckForUpdates": "检查更新",
    "Language": "多语言设置",
    "DiscoverNewVersions": "发现新版本",
    "describe": "新版本更新内容",
    "item": "取消 | 确定"
  }
}
</i18n>

<template>
  <div class="sidebar">
    <transition name="delay">
      <div class="mask" v-show="sidebarShow" @click="openSidebar"></div>
    </transition>
    <transition name="fade">
      <div class="sidebar-container" v-show="sidebarShow">
        <img src="../../assets/sidebar_images/head.png" alt="" @click="pageJump(1)">
        <span @click="pageJump(1)" v-html="isLogin?userInfo.name:$t('NotLoggedIn')"></span>
        <ul class="list">
          <li  @click="pageJump(2)">
            <a>
              <img src="../../assets/sidebar_images/bag@3x.png" alt="">
              <strong>{{ $t('MyWallet') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(3)">
            <a>
              <img src="../../assets/sidebar_images/list@3x.png" alt="">
              <strong>{{ $t('TheOrder') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(4)">
            <a>
              <img src="../../assets/sidebar_images/aboutus@3x.png" alt="">
              <strong>{{ $t('AboutUs') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(5)">
            <a>
              <img src="../../assets/sidebar_images/idea@3x.png" alt="">
              <strong>{{ $t('Opinion') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(6)">
            <a>
              <img src="../../assets/sidebar_images/chink@3x.png" alt="">
              <strong>{{ $t('CheckForUpdates') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(7)">
            <a>
              <img src="../../assets/sidebar_images/languagesetting@3x.png" alt="">
              <strong>{{ $t('Language') }}</strong>
            </a>
          </li>
        </ul>
      </div>
    </transition>

    <!-- 弹框 -->
    <message-box :messageBox="messageBox"
                  :index="1"
                  icon="Update"
                  :title="$t('DiscoverNewVersions')"
                  :describe="$t('describe')"
                  :confirm="$tc('item',2)"
                  :cancel="$tc('item',1)"
                  @boxHandle="boxHandle"></message-box>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import messageBox from '../../components/MessageBox/index'
export default {
  name: 'sidebar',

  props: {
    sidebarShow: {
      type: Boolean,
      default: false
    }
  },

  components: {
    messageBox
  },

  data () {
    return {
      messageBox: false
    }
  },

  computed: {
    ...mapGetters([
      'userInfo', 'isLogin'
    ])
  },

  methods: {
    // 关闭侧边栏
    openSidebar () {
      this.$emit('openSidebar', false)
    },

    boxHandle (confirm, cancel) {
      if (confirm) {
        console.log(1111)
      }
      this.messageBox = false
    },

    // 跳转相应界面
    pageJump (num) {
      let page = {
        1: '/personal',
        2: '/wallet',
        3: '/order',
        4: '/about',
        5: '/feedback',
        7: '/language'
      }
      this.$router.push({path: page[num]})
      if (this.ISDEBUG && num === 6) {
        this.messageBox = true
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.sidebar{
  .mask{
    .fs;
    z-index: 10;
    background: rgba(0, 0, 0, .5)
  }
  .sidebar-container{
    .wh(550px, 100%);
    .fs;
    z-index: 10;
    background: #FFF;
    padding-top: @statusBar;
    img{
      height: 260px;
      margin: 50px auto 0px auto;
    }
    span{
      font-size: 35px;
      margin-top: -18px;
      color: #646464;
      display: block;
      height: 40px;
    }
    .list{
      .wh(100%, auto);
      margin-top: 89px;
      li{
        .wh-l(100%, 80px);
        position: relative;
        text-align: left;
        a{
          .wh(100%, 100%);
          display: block;
          img{
            height: 30px;
            position: absolute;
            left: 5%;
            top: 50%;
            margin-top: -18px;
          }
          strong{
            .wh(auto, 100%);
            font-size: 30px;
            display: block;
            margin-left: 18%;
          }
        }
      }
    }
  }
}

// .delay-leave-active{
//   transition-delay: .3s;
// }
.fade-enter-active,.fade-leave-active{
  transition: all .25s ease-out;
}
.fade-enter, .fade-leave-to{
  transform: translate3d(-100%, 0, 0);
  opacity: 0;
}
</style>



