<template>
  <div class="picker">
    <div class="picker-content">
      <div class="mask-top"></div>
      <div class="mask-bottom"></div>
      <div class="wheel-wrapper" ref="wheelWrapper">
        <div class="wheel" v-for="(data, index) in pickerData" :key="index">
          <ul class="wheel-scroll">
            <li v-for="(item, index2) in data" :key="index2" class="wheel-item" :class="{active: pickerSelectedIndex[index]===index2}">{{item}}</li>
          </ul>
          <div class="unit">{{unit[index]}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'

const STATE_HIDE = 0
const STATE_SHOW = 1
export default {
  name: 'picker',
  props: {
    data: {
      type: Array,
      default () {
        return []
      }
    },
    selectedIndex: {
      type: Array,
      default () {
        return []
      }
    },
    unit: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      state: STATE_HIDE,
      pickerData: this.data.slice(),
      pickerSelectedIndex: this.selectedIndex,
      selectedArray: []
    }
  },
  created () {
    if (!this.pickerSelectedIndex.length) {
      this.pickerSelectedIndex = []
      for (let i = 0; i < this.pickerData.length; i++) {
        this.pickerSelectedIndex[i] = 0
      }
    }
    if (!this.unit.length) {
      this.unit = []
      for (let i = 0; i < this.pickerData.length; i++) {
        this.unit[i] = ''
      }
    }
  },

  methods: {
    getInitValue () {
      let initValue = []
      for (let i = 0; i < this.pickerData.length; i++) {
        initValue.push(this.pickerData[i][this.pickerSelectedIndex[i]])
      }
      return initValue
    },

    show () {
      if (this.state === STATE_SHOW) {
        return
      }
      this.state = STATE_SHOW

      if (!this.wheels || this.dirty) {
        this.$nextTick(() => {
          this.wheels = []
          let wheelWrapper = this.$refs.wheelWrapper
          for (let i = 0; i < this.pickerData.length; i++) {
            this._createWheel(wheelWrapper, i)
          }
          this.dirty = false
        })
      } else {
        for (let i = 0; i < this.pickerData.length; i++) {
          this.wheels[i].enable()
          this.wheels[i].wheelTo(this.pickerSelectedIndex[i])
        }
      }
    },

    hide () {
      this.state = STATE_HIDE

      for (let i = 0; i < this.pickerData.length; i++) {
        this.wheels[i].disable()
      }
    },

    _createWheel (wheelWrapper, i) {
      if (!this.wheels[i]) {
        this.wheels[i] = new BScroll(wheelWrapper.children[i], {
          wheel: {
            selectedIndex: this.pickerSelectedIndex[i],
            rotate: 25,
            /** 默认值就是下面配置的两个，为了展示二者的作用，这里再配置一下 */
            wheelWrapperClass: 'wheel-scroll',
            wheelItemClass: 'wheel-item'
          },
          probeType: 3
        })

        this.wheels[i].on('scroll', (pos) => {
          let index
          if (this.wheels[i].movingDirectionY === 1) {
            index = Math.floor(Math.abs(pos.y - this.wheels[i].itemHeight + 4) / this.wheels[i].itemHeight)
          } else if (this.wheels[i].movingDirectionY === -1) {
            index = parseInt(Math.abs(pos.y) / this.wheels[i].itemHeight)
          }
          this.$set(this.pickerSelectedIndex, i, index)
          // console.log(pos.y)
          // console.log(this.wheels[i])
          // console.log(index)
          // console.log(this.pickerSelectedIndex)
        })
        this.wheels[i].on('scrollEnd', (pos) => {
          this.selectedArray = []
          for (let i = 0; i < this.pickerData.length; i++) {
            let index = this.wheels[i].getSelectedIndex()
            this.selectedArray.push(this.pickerData[i][index])
            // 滚动结束赋值下标
            this.$set(this.pickerSelectedIndex, i, index)
          }
          this.$emit('change', this.selectedArray)
        })
      } else {
        this.wheels[i].refresh()
      }
      return this.wheels[i]
    },

    _canConfirm () {
      return this.wheels.every((wheel) => {
        return !wheel.isInTransition
      })
    },

    setData (data) {
      this.pickerData = data.slice()
      this.dirty = true
    },

    scrollTo (index, dist) {
      const wheel = this.wheels[index]
      this.pickerSelectedIndex[index] = dist
      wheel.wheelTo(dist)
    }
  },

  watch: {
    data (newData) {
      this.setData(newData)
    }
  }
}
</script>

<style lang="less" scoped>
.picker{
  .wh(100%, 100%);
  padding: 12% 0;
  .picker-content{
    .wh(100%, 100%);
    position: relative;
    .mask-top{
      .wh(100%, 80px);
      position: absolute;
      top: 0;
      z-index: 10;
      transform: translateZ(0);
      pointer-events: none;
      background: linear-gradient(to top, rgba(255,255,255,0.4), rgba(255,255,255,0.8));
    }
    .mask-bottom{
      .wh(100%, 80px);
      position: absolute;
      bottom: 1px;
      z-index: 10;
      transform: translateZ(0);
      pointer-events: none;
      background: linear-gradient(to bottom, rgba(255,255,255,0.4), rgba(255,255,255,0.8));
    }
    .wheel-wrapper{
      height: 100%;
      display: flex;
      justify-content: space-around;
      .wheel{
        .wh(100%, 100%);
        overflow: hidden;
        position: relative;
        .wheel-scroll{
          margin-top: 84px;
          .active{
            transition: all .1s ease-in !important;
            transform: scale(2) !important;
          }
          li{
            .wh-l(100%, 90px);
            font-size: 40px;
          }
        }
        .unit{
          .wh(50px, 50px);
          position: absolute;
          top: 50%;
          right: 20%;
          font-size: 30px;
          margin-top: -25px;
        }
      }
    }
  }
}
</style>



