<i18n>
{
  "en": {
    "waitOrder": "Please wait while the order is being paid.",
    "OrderSuccessfully": "Order payment successfully"
  },
  "zh": {
    "waitOrder": "正在支付该订单请稍候",
    "OrderSuccessfully": "订单支付成功"
  }
}
</i18n>

<template>
  <transition name="fade">
    <div class="messageTip" v-if="payTip===1||payTip===2||payTip===3">
      <div class="pay" v-if="payTip===1">
        <img src="../../../assets/scanning_images/payloading.png" alt="">
        <span>{{ $t('waitOrder') }}</span>
      </div>
      <div class="paySuccess" v-if="payTip===2">
        <img src="../../../assets/scanning_images/succeed-icon.png" alt="">
        <span>{{ $t('OrderSuccessfully') }}</span>
      </div>
      <div class="paySuccess" v-if="payTip===3">
        <img src="../../../assets/scanning_images/succeed-icon.png" alt="">
        <span>您已成功归还充电宝</span>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'messageTip',
  props: {
    payTip: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {

    }
  }
}
</script>

<style lang="less" scoped>
@keyframes waiting{
  0% {
    transform: rotate(0deg)
  }
  100% {
    transform: rotate(360deg)
  }
}
.messageTip{
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  .pay, .paySuccess{
    .wh(260px, 240px);
    border-radius: 8px;
    position: absolute;
    left: 50%;
    top: 37%;
    margin-left: -130px;
    background: #FFF;
    box-shadow: 0 3px 20px 3px rgba(0,0,0,.2);
  }
  .pay{
    img{
      width: 74px;
      position: absolute;
      top: 20%;
      left: 50%;
      margin-left: -37px;
      transform-origin: center;
      animation: waiting 1s ease infinite;
    }
    span{
      .wh(100%, 100px);
      font-size: 26px;
      display: block;
      padding: 0 10%;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
  .paySuccess{
    img{
      width: 74px;
      position: absolute;
      top: 18%;
      left: 50%;
      margin-left: -37px;
    }
    span{
      .wh(100%, 100px);
      font-size: 26px;
      display: block;
      padding: 0 10%;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
}

.fade-enter-active,.fade-leave-active{
  transition: all .2s ease-out;
}
.fade-enter, .fade-leave-to{
  transform: scale(.8,.8);
  opacity: 0;
}
</style>



