<i18n>
{
  "en": {
    "all": "All",
    "charger": "Charger",
    "bettery": "Bettery",
    "QRcode": "Scan QR code",
    "commonProblems": "Common problems",
    "instructions": "Instructions",
    "contactCustomer": "Contact customer service",
    "cancel": "Cancel",
    "EquipmentOffLine": "Equipment off-line",
    "changeEquipment": "The equipment can not be used for the time being. Please change the equipment.",
    "OK": "OK",
    "Reminder": "Reminder",
    "waitForEnd": "Charging is currently in use. Please wait for the end of the charge to try again.",
    "reScanQRcode": "The page has expired, please re-scan the cabinet qr code",
    "credit": "Sorry, your credit is running low",
    "rechargeTheBalance": "In order to normally use the filling charge, please recharge the balance first.",
    "RechargeBalance": "Recharge balance"
  },

  "zh": {
    "all": "全部",
    "charger": "座充",
    "bettery": "充电宝",
    "QRcode": "扫码充电",
    "commonProblems": "常见问题",
    "instructions": "使用说明",
    "contactCustomer": "联系客服",
    "cancel": "取消",
    "EquipmentOffLine": "设备离线",
    "changeEquipment": "该设备暂时不能使用，请换台设备使用",
    "OK": "好的",
    "Reminder": "提示",
    "waitForEnd": "当前已经在使用座充充电，请等待结束充电后，再重试",
    "reScanQRcode": "页面已超时，请重新扫描机柜二维码",
    "credit": "余额不足",
    "rechargeTheBalance": "为了正常使用座充，请先充值余额",
    "RechargeBalance": "充值余额"
  }
}
</i18n>
<template>
  <div class="home">
    <header class="bomPixel">
      <div class="statusBar"></div>
      <div class="nav-left" @click="openSidebar(true)">
        <img src="../../assets/home_images/Head.png">
      </div>
      <div class="nav-center">
        <ul>
          <li @click="navigation(1)" :class="{activeColor:navActive===1}">
            {{ $t('all') }}
            <img v-show="navActive===1" src="../../assets/home_images/sliding.png">
          </li>
          <li @click="navigation(2)" :class="{activeColor:navActive===2}">
            {{ $t('charger') }}
            <img v-show="navActive===2" src="../../assets/home_images/sliding.png">
          </li>
          <li @click="navigation(3)" :class="{activeColor:navActive===3}">
            {{ $t('bettery') }}
            <img v-show="navActive===3" src="../../assets/home_images/sliding.png">
          </li>
        </ul>
      </div>
      <div class="nav-right"  @click="openStoreSort">
        <img src="../../assets/home_images/seach.png">
      </div>
    </header>
    <div class="home-container" :class="{filterClass: payTip===1||payTip===2||payTip===3}">
      <!-- 地图 -->
      <map-module ref="AMap"></map-module>
      <div class="button">
        <img class="btn" src="../../assets/home_images/btn.png">
        <span @click="bomHandle(1)">
          <img src="../../assets/home_images/scan.png">
          <em>{{ $t('QRcode') }}</em>
        </span>
        <div class="positioning" @click="bomHandle(2)">
          <img src="../../assets/home_images/restoration.png">
        </div>
        <div class="service" @click="bomHandle(3)">
          <img src="../../assets/home_images/service.png">
        </div>
      </div>

      <!-- 店铺信息 -->
      <store-column v-if="false"></store-column>

      <!-- 充电时长框 -->
      <charging-time :chargingTime="chargingTime"
                     :chargingTimeValue="chargingTimeValue"></charging-time>
    </div>

    <!-- 侧边栏 -->
    <sidebar :sidebarShow="sidebarShow"
             @openSidebar="openSidebar"></sidebar>

    <!-- 座充扫码 -->
    <time-column :timePicker="timePicker"
                 @timeHandle="timeHandle"></time-column>

    <money-tip  :moneyTip="moneyTip"
                :totalAmount="totalAmount"
                @moneyHandle="moneyHandle"></money-tip>

    <!-- 弹框 -->
    <message-box :messageBox="messageBox"
                  :index="1"
                  icon="offLine"
                  :title="$t('EquipmentOffLine')"
                  :describe="$t('changeEquipment')"
                  :alert="$t('OK')"
                  @boxHandle="boxHandle"></message-box>

    <message-box :messageBox="messageTip"
                  :index="2"
                  icon="battery"
                  :title="$t('Reminder')"
                  :describe="$t('waitForEnd')"
                  :alert="$t('OK')"
                  @boxHandle="boxHandle"></message-box>

    <message-box :messageBox="timeout"
                  :index="3"
                  icon="scan"
                  :title="$t('Reminder')"
                  :describe="$t('reScanQRcode')"
                  :alert="$t('OK')"
                  @boxHandle="boxHandle"></message-box>

    <message-box :messageBox="recharge"
                  :index="4"
                  icon="defic"
                  :title="$t('credit')"
                  :describe="$t('rechargeTheBalance')"
                  :alert="$t('RechargeBalance')"
                  @boxHandle="boxHandle"></message-box>

    <!-- 操作表 -->
    <action-sheet :actionSheet="actionSheet"
                  :index="1"
                  :title="$t('commonProblems')"
                  :list="[$t('instructions'), $t('contactCustomer')]"
                  :cancel="$t('cancel')"
                  color="#575757"
                  @sheetHandle="sheetHandle"></action-sheet>

    <!-- 支付提示框 -->
    <pay-tip :payTip="payTip"></pay-tip>

    <transition name="fade-effect" mode="in-out">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { numToTimeFormat } from '../../utils/index.js'
import sidebar from '../sidebar/index'
import mapModule from './components/amap/index'
import storeColumn from './components//StoreColumn'
import timeColumn from '../scanning/scanCode/timeColumn'
import moneyTip from '../scanning/scanCode/money'
import messageBox from '../../components/MessageBox/index'
import actionSheet from '../../components/ActionSheet/index'
import payTip from './components/payTip'
import chargingTime from './components/chargingTime'
export default {
  name: 'home',
  data () {
    return {
      navActive: 1, // 头部导航选择
      clickType: '', // 点击类型（1 扫码）
      messageBox: false, // 弹框显示隐藏
      messageTip: false,
      timeout: false,
      recharge: false,
      actionSheet: false, // 操作表
      timePicker: false, // 时间选择框
      totalTime: 60, // 总时间
      moneyTip: false, // 金额提示框
      totalAmount: 0, // 总金额
      payTip: 0, // 支付提示框  0、关闭 1、正在支付 2、支付完成 3、成功归还支付宝
      chargingTime: false // 剩余充电时长框
    }
  },

  components: {
    sidebar,
    mapModule,
    storeColumn,
    messageBox,
    timeColumn,
    moneyTip,
    payTip,
    chargingTime,
    actionSheet
  },

  computed: {
    ...mapGetters([
      'sidebarShow', 'isLogin', 'chargerId'
    ]),
    chargingTimeValue () {
      return numToTimeFormat(this.totalTime)
    }
  },

  mounted () {
    this.DATA_RESPONSE()

    // 查询系统语言请求
    this.dataInteractionRequest({
      msgType: '0x5041'
    })
  },

  activated () {
    this.DATA_RESPONSE()
  },

  methods: {
    ...mapActions([
      'sidebarSwitch', 'setLogin', 'setChargerId', 'loadingAlert'
    ]),

    // 响应接口
    DATA_RESPONSE () {
      // 归还充电宝界面传值
      if (this.$route.params.payTip === 3) {
        this.payTip = 3
        let timer = setTimeout(() => {
          this.payTip = 0
          clearTimeout(timer)
        }, 2000)

        if (this.ISDEBUG) {
          // let timer2 = setTimeout(() => {
          //   let str = '{"msgType": "0x1205","result":true,"data":{"state": 1}}'
          //   window.dataInteractionResponse(str)
          //   clearTimeout(timer2)
          // }, 4000)
        }
      }
      this.dataInteractionResponse(data => {
        if (data.msgType === '0x5001') {
          // 按返回物理按键退出请求
          this.dataInteractionRequest({
            msgType: '0x5002'
          })
        } else if (data.msgType === '0x5102') {
          // 检验是否登录录返回（0 未登录、1 已登录）
          if (data.result && data.data.state === 1) {
            this.setLogin(true)

            // 点击扫描数据返回
            if (this.clickType === 1) {
              this.loadingAlert(true)
              // 调用摄像头扫描二维码请求
              this.dataInteractionRequest({
                msgType: '0x5201'
              })
              this.clickType = ''
            }
          } else if (data.result && data.data.state === 0) {
            this.$router.push('/login')
          }
        } else if (data.msgType === '0x5202') {
          // 扫描二维码数据返回
          if (data.result) {
            this.setChargerId(data.data)
            // 查询设备是否在线请求
            this.dataInteractionRequest({
              msgType: '0x5203',
              imei: data.data.imei
            })
          }
        } else if (data.msgType === '0x5204') {
          // 查询设备是否在线返回
          if (data.result) {
            if (data.data.isOnline) {
              this.$router.push('/charger')
            } else {
              this.messageBox = true
            }
          }
        }
      })
    },

    // 头部导航
    navigation (num) {
      this.navActive = num
    },

    // 侧边栏
    openSidebar (state) {
      this.sidebarSwitch(state)
    },

    // 店铺
    openStoreSort () {
      this.$router.push({path: '/store'})
    },

    // 时间选择框
    timeHandle (confirm, time) {
      if (confirm) {
        this.totalTime = parseInt(time[0]) * 60 + parseInt(time[1])
      }

      if (this.ISDEBUG) {
        if (confirm) {
          this.moneyTip = true
          console.log(time)
        }
        this.timePicker = false
      }
    },

    // 金额提示框
    moneyHandle (confirm) {
      if (!this.ISDEBUG) {
        if (confirm) {
        } else {
          this.moneyTip = false
        }
      }

      if (this.ISDEBUG) {
        if (confirm) {
          this.$http('/scanning/1').then(res => {
            if (res.data.result) {
              this.moneyTip = false
              this.payTip = 1
              let timer = setInterval(() => {
                if (++this.payTip > 2) {
                  this.payTip = 0
                  clearInterval(timer)
                  // 剩余充电时长
                  this.chargingTime = true
                }
              }, 2000)
            } else {
              this.moneyTip = false
              this.recharge = true
            }
          })
        } else {
          this.moneyTip = false
        }
      }
    },

    // 弹框
    boxHandle (confirm, cancel, index) {
      switch (index) {
        case 1:
          this.messageBox = false
          break
        case 2:
          this.messageTip = false
          break
        case 3:
          this.timeout = false
          break
        case 4:
          this.recharge = false
          if (confirm) {
            this.$router.push('/recharge')
          }
          break
      }
    },

    // 操作表
    sheetHandle (confirm, value, index) {
      this.actionSheet = false
    },

    bomHandle (num) {
      switch (num) {
        // 扫码
        case 1:
          if (this.ISDEBUG) {
            this.$http('/scanning/index').then(res => {
              this.setChargerId({'imei': '1000000009', 'merchantId': 114})
              console.log(res.data.type)
              if (res.data.type === 'lease') {
                // 剩余充电时长
                this.chargingTime = false
                this.$router.push({path: '/charger'})
              } else if (res.data.type === 'restitute') {
                // 剩余充电时长
                this.chargingTime = false
                this.$router.push({path: '/charger'})
              } else if (res.data.type === 'scanCode') {
                this.timePicker = true
              }
            })
          }
          if (!this.isLogin) {
            this.clickType = 1
            // 检验是否登录请求
            this.dataInteractionRequest({
              msgType: '0x5101'
            })
          } else {
            this.loadingAlert(true)
            // 调用摄像头扫描二维码请求
            this.dataInteractionRequest({
              msgType: '0x5201'
            })
          }
          break
        // 定位
        case 2:
          this.$refs.AMap.setCenter()
          break
        // 客服
        case 3:
          this.actionSheet = true
          break
      }
    }
  }
}
</script>

<style scoped lang="less">
.home{
  .wh(100%, 100%);
  position: relative;
  header{
    .wh(100%, @statusBar + @headHeight);
    background: #FFF;
    position: relative;
    .statusBar{
      .wh(100%, @statusBar);
      background: @statusBarColor;
    }
    .nav-left{
      .wh-l(110px, @headHeight);
      float: left;
      position: relative;
      img{
        height: 42px;
        .f-center;
      }
    }
    .nav-center{
      .wh-l(calc(~"100% - 220px"), @headHeight);
      float: left;
      font-size: 36px;
      .activeColor{
        color: #F5D828;
      }
      ul{
        .wh(auto, 100%);
        margin: 0 auto;
        display:inline-block;
        li{
          position: relative;
          padding: 0 15px;
          img{
            width: 50px;
            position: absolute;
            left: 50%;
            margin-left: -25px;
            bottom: 9px;
          }
        }
        li:nth-child(1){
          .wh-f(auto, 100%);
        }
        li:nth-child(2){
          .wh-f(auto, 100%);
        }
        li:nth-child(3){
          .wh-f(auto, 100%);
        }
      }
    }
    .nav-right{
      .wh-l(110px, @headHeight);
      float: left;
      position: relative;
      img{
        height: 42px;
        left: 46% !important;
        .f-center;
      }
    }
  }
  .filterClass{
    filter: blur(10px);
  }
  .home-container{
    .fs-noTop;
    background: #FFF;
    overflow: hidden;
    .button{
      .wh(720px, 160px);
      position: absolute;
      left: 50%;
      margin-left: -360px;
      bottom: 10px;
      .btn{
        .wh(100%, 100%);
      }
      span{
        .wh-l(100%, 100px);
        position: absolute;
        left: 0;
        top: 20px;
        font-size: 30px;
        line-height: 110px;
        img{
          height: 38px;
          position: relative;
          left: 0;
          top: 10px;
          margin-right: 25px;
        }
      }
      .positioning{
        .wh(90px, 90px);
        position: absolute;
        left: 20px;
        top: -100px;
        img{
          height: 90px;
        }
      }
      .service{
        .wh(90px, 90px);
        position: absolute;
        right: 20px;
        top: -100px;
        img{
          height: 90px;
        }
      }
    }
  }
}
</style>



